'use strict';

var ipRegex = require('ip-regex');

var isIp = function isIp(string) {
  return ipRegex({
    exact: true
  }).test(string);
};

isIp.v4 = function (string) {
  return ipRegex.v4({
    exact: true
  }).test(string);
};

isIp.v6 = function (string) {
  return ipRegex.v6({
    exact: true
  }).test(string);
};

module.exports = isIp;